/* Styles for the container */
.containerlog {
    position: relative;
    width: 100%;
    height: 100vh;
  }
  
  /* Styles for the background image */
  .backgroundimg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('../images/logsinback.jpg');
    background-size: cover;
    filter: blur(8px);
    opacity: 0.7; /* Adjust the opacity for brightness */
  }
  
  /* Styles for the centered box */
  .center-box {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.8); /* Adjust the opacity for brightness */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Add shadow effect */
  }
  
  /* Styles for the login text */
  .login-text {
    font-weight: bold;
    font-size: 2rem; /* Adjust font size as needed */
  }
  