.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
  }
  
  .cards {
    width: 250px;
    border-radius: 5px;
    overflow: hidden; /* Ensure content doesn't overflow */
    transition: transform 0.3s ease;
  }
  
  .cards:hover {
    transform: scale(1.2); /* Enlarge the card on hover */
  }
  
  .cards img {
    width: 100%;
    object-fit: cover;
    object-position: top;
  }
  
  .card-content {
    padding: 10px;
    background-color: #f9f9f9;
    text-align:center;
  }