.reviewbox{
    height:600px;
    width: 1000px;
    gap:10px;
    margin: 5px auto;
    display:grid;
    grid-template-columns: 200px 200px 200px 200px 200px;
    grid-template-rows: 200px 200px 200px;
    margin-bottom: 30px;
  }
  
  .revbox1 {
    position: relative;
    width: 200px;
    height: 200px;
  }
  
  .revimg {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .textforover {
    position: absolute;
    width:100%;
    height:100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    text-align: center;
    padding: 10px;
    display: none; /* Initially hide the text */
  }
  
  .revbox1box:hover .textforover {
    display: block; /* Show text on hover */
  }
  
  .revbox1:hover .textforover {
    display: block; /* Show text on hover */
  }
  
  .revbox2 {
    position: relative;
    width: 200px;
    height: 200px;
    overflow: hidden; /* Hide overflowing image */
    background-color: white;
  }
  
  .defaulttext {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.5); /* Semi-transparent white background */
    color: black; 
    display: flex;
    justify-content: center;
    align-items: center;
    text-align:center;
    display:block;
    padding:10px;
  }
  
  .hoverimage {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0; /* Initially hide the image */
  }
  
  .revbox2:hover .defaulttext {
    display: none; /* Hide text on hover */
  }
  
  .revbox2:hover .hoverimage {
    opacity: 1; /* Show image on hover */
  }