.informationbox {
    position: relative;
    margin-top:40px;
   /* Adjust as needed */
    height: 400px; /* Adjust as needed */
  }
  
  .image-container {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  
  .background-image {
    width: 100%;
    height: 100%;
    transition: filter 0.3s ease;
  }
  
  .text-overlay {
    position: absolute;
    height:100%;
    width:100%;
    text-align:center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(0, 0, 0, 0.5); /* Adjust as needed */
    padding: 10px;
    border-radius: 5px;
    opacity: 0;
    transition: opacity 0.3s ease;
    text-decoration: none;
  }
  
  .text {
    color: white; /* Adjust as needed */
    text-decoration: none;
    margin-top:50px;
  }
  
  .image-container:hover .background-image {
    filter: brightness(70%); /* Adjust brightness level as needed */
  }
  
  .image-container:hover .text-overlay {
    opacity: 1;
  }