/* Styles for the container */
.containerlog {
    position: relative;
    width: 100%;
    height: 100vh;
  }
  
  /* Styles for the background image */
  .backgroundimg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('../images/logsinback.jpg');
    background-size: cover;
    filter: blur(8px);
    opacity: 0.7; /* Adjust the opacity for brightness */
  }
  
  /* Styles for the centered box */
.center-box {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 400px; /* Adjust the width as needed */
    padding: 40px; /* Add more padding for a larger box */
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2); /* Add shadow effect */
  }
  
  /* Styles for the login text */
  .login-text {
    font-weight: bold;
    font-size: 2rem;
    text-align: center; /* Center the login text */
    margin-bottom: 30px; /* Add some spacing below the login text */
  }
  
  /* Styles for the form group */
  .form-group {
    margin-bottom: 20px; /* Add spacing between form elements */
  }
  
  /* Styles for the input fields */
  input[type='text'],
  input[type='password'] {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
  }
  
  /* Styles for the checkbox */
  label {
    display: block;
    margin-bottom: 10px;
  }
  
  /* Styles for the login button */
  button[type='submit'] {
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button[type='submit']:hover {
    background-color: #0056b3;
  }
  
  