body{
    margin:0;
}

.grid{
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    grid-template-rows: 300px 300px;
    height: 500px;
    width:100%;
    background-image: url(../images/caroback.png);
    background-size: 100% 100%;
}
#box2 {
    grid-column: 1 / 3;
    grid-row: 1/3; 
    text-align: center;
    margin-top:50px;
    font-size: 30px;
    font-weight: bold;
  }
.card{
    width: 250px;
    height: 380px;
    box-shadow: 0 0 10px rgba(145, 145, 203, 0.653);
    text-align: center;
    padding: 10px;
    margin: 20px auto;
    
    
}
.textincar{
    font-size: 18px;
    font-weight: bold;
    padding:20px;
}
