.twocolbox {
    display: grid;
    grid-template-columns: 1fr 1fr; /* Equal width columns */
    gap: 20px; /* Gap between columns */
    padding: 20px;
    background-color: #ccc;
    margin-top: 30px;
  }
  
  .col1 {
    text-align: left;
  }
  
  .col2 {
    text-align: left;
  }
  
  /* Styles for the form elements */
  label {
    display: block; /* Ensure labels appear on new lines */
    margin-bottom: 5px;
  }
  
  input[type="text"],
  input[type="email"],
  textarea {
    width: 100%; /* Make inputs and textarea take up full width */
    padding: 8px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  button {
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  